"use client";

import Link from "next/link";
import { useState } from "react";
import { Bell, User } from "lucide-react";

export default function Layout({ children }: { children: React.ReactNode }) {
  const [showProfileMenu, setShowProfileMenu] = useState(false);

  const handleLogout = () => {
    window.location.href = "/login";
  };

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Navbar */}
      <nav className="flex items-center justify-between bg-blue-600 p-5 text-white shadow">
        <div className="flex items-center space-x-10">
        <div className="text-blue-600 text-3xl">âž•</div>
          <h1 className="text-2xl font-bold">MASS</h1>

          <span className="text-lg font-medium hover:underline cursor-default">
            Book Appointment
          </span>
          <Link href="/patientappointments" className="text-lg font-medium hover:underline">
  My Appointments
</Link>

          
<Link href="/patientpayments" className="text-lg font-medium hover:underline">
  Payment History
</Link>

          
          <span className="text-lg font-medium hover:underline cursor-default">
            Chat Support
          </span>

          <Bell className="w-6 h-6 cursor-pointer hover:text-blue-300" />
        </div>

        {/* Profile Dropdown */}
        <div className="relative">
          <button
            className="flex items-center space-x-2"
            onClick={() => setShowProfileMenu(!showProfileMenu)}
          >
            <User className="w-6 h-6" />
            <span className="font-medium">Jacob Turner</span>
          </button>

          {showProfileMenu && (
  <div className="absolute right-0 mt-3 w-40 bg-white shadow rounded-lg overflow-hidden text-gray-800 z-50">
    <Link
      href="/update-details"
      className="block px-4 py-2 hover:bg-gray-100"
    >
      Update Details
    </Link>
    <button
      className="w-full text-left px-4 py-2 hover:bg-gray-100"
      onClick={handleLogout}
    >
      Logout
    </button>
  </div>
)}

        </div>
      </nav>

      <main className="p-6">{children}</main>
    </div>
  );
}



